import{ mapState,mapMutations} from 'vuex'
// import actionlist from "./selfCenterList.vue"
// import actionlist from "@/components/actionlist.vue"
export default {
	data() {
		return {
			tab:0,//行动，围观，收藏
			list:[1,2,3,4,5],
			userInfo:'',
		}
	},
	computed: {
	           ...mapState(['hasLogin'])  
	       }, 
   onShow() {
	if(this.userInfo==''||this.userInfo==undefined||this.userInfo==null){
		try{
			
			this.userInfo=uni.getStorageSync('userInfo')
			console.log(this.userInfo)
		}catch(e){
							   console.log(Error)
						   };
		
	}
   },
   
	onLoad() {
		if(!this.hasLogin){
			uni.redirectTo({
				url: '../login/login' 
			});
			return false;
		};
		try{
			
			this.userInfo=uni.getStorageSync('userInfo')
			console.log(this.userInfo)
		}catch(e){
							   console.log(Error)
						   };
	},
	methods: {
		goPage(url){
			uni.navigateTo({
				url
			});
		},
		
		clickMe: function () {
			let that=this;	
					console.log('支付')
		              wx.getSetting({
		                success: res => {
							console.log(res)
		                  if (res.authSetting['scope.userInfo']) {
		                    that.xd_request_post(that.xdServerUrls.xd_pay,
							{
								unionId:that.userInfo.unionId,
								openid:that.userInfo.openId,
								userName:that.userInfo.nickName,
								id:uni.getStorageSync('id'),
								token:uni.getStorageSync('token'),
								userHead:that.userInfo.avatarUrl,
								city:that.userInfo.city,
								province:that.userInfo.province,
								payRmb:1,
								
							},false).then(res=>{	
								console.log(res)
		                    	uni.requestPayment({
		                    		 'appId': res.obj.appId,
		                    		'timeStamp': res.obj.timeStamp,
		                    		'nonceStr': res.obj.nonceStr,
		                    		'package': res.obj.packageAlias,
		                    		'signType': 'MD5',
		                    		'paySign': res.obj.paySign,
		                    		success: function (res) {
		                    			that.xd_request_post(that.xdServerUrls.xd_resultCallBack,{},false).then( res=>{
		                    			})
		                    			uni.showToast({
		                    				title: '微信支付成功',
		                    				icon: 'success',
		                    				duration: 1500
		                    			});
		                    			uni.reLaunch({
		                    				url: '../index/index'
		                    			})
		                    		},
		                    		fail: function (err) {
		                    			// 支付失败的回调中 用户未付款
		                    			uni.showModal({
		                    				content:'支付取消',
		                    				confirmText:'继续支付',
		                    				cancelText:'返回首页',
		                    				image:'/static/images/icon/clock.png',
		                    				success:function(res) {
		                    					 if (res.confirm) {
		                    						return false
		                    					} else if (res.cancel) {
		                    						uni.reLaunch({
		                    							url: '../index/index',
		                    							})
		                    						}
		                    					},
		                    					
		                    			});
		                    		}
		                    	});
		                    })
		
		                      }
		                    }
		              })
		

		 },
	},
	// components:{
	// 	actionlist
	// }
}
